<template>
    <div class="app-login">
        <h2 class="wel-login">OA</h2>
        <div class="cover">
            <div class="loginimg">
                <img src="../../assets/login.png">
            </div>
            <div class="l-form">
                <div>
                    <span></span>
                    <input v-model="uname" type="text" placeholder="">
                </div>
                <div>
                    <span></span>
                    <input type="password" v-model="upwd" placeholder="">
                </div>
                <mt-button class="btn" @click="login"></mt-button>
            </div>
       </div>
    </div>
</template>
<script>
export default {
    // 1. uname upwd
     data() {
        return {
            uname:"", //
            upwd:""   //
        }
    },
    // 2.login 
    methods: {
       login(){
        // :
        // 1. 3~12
        var reg = /^[a-z0-9]{3,12}$/i;
        // 2. /
        var u = this.uname;
        var p = this.upwd;
        // console.log(u+"_"+p);
        // 3. 
        if(!reg.test(u)){
            this.$messagebox("","");
            return;
        }
        // 4. 
        if(!reg.test(p)){
             this.$messagebox("","");
            return;
        }
        // 5.ajax
        var url = "login";
        var obj = {uname:u,upwd:p};
        this.axios.get(url,{params:obj}).then(res=>{//
            // console.log(res)
            if(res.data.code==1){
                this.$toast("")
                this.$router.push("/Index")
            }else{
                this.$toast("")
            }
        })
        .catch(err=>{//
            console.log(err)
        })
       }
    },
}
</script>
<style scoped>
    .app-login{
        overflow: hidden;
        height:100vh; /*100%*/
        background-size:100% 100%; /*100%*/
        background: url(../../assets/login.jpg) no-repeat;
        text-align: center;
    }
    /*  */
    .wel-login{
        color: #fff;
        margin-top: 30%
    }
    .cover{
        width: 70%;
        background: rgba(0, 0, 0, 0.3);
        margin:  0 auto;
        color: #fff;
        padding: 45px 0;
    }
    .l-form input{
        border: 0;
        background: 0;
        outline: none;/**/
        color: #fff
    }
    .l-form div{
        border-bottom: 1px solid #333; 
        margin: 30px 0;
    }
    .l-form{
        width: 250px;
        margin: 0 auto
    }
    .btn{
       width: 50%;
       background: #18a8f1;
       color: #fff;
    }
    input::-webkit-input-placeholder {
        color: rgba(238, 238, 238, 0.788);
      }
</style>